<script setup>
import MsgForm from '@/components/MsgForm/MsgForm.vue';
import { ref } from 'vue'
import axios from 'axios';


let message = ref([]);



const findMessage = () => {
    axios.get('http://localhost:3001/api/message/add')
        .then(response => {
            console.log('yes');
            message.value = response.data; // 更新 message 为新的数据  
        })
        .catch(error => {
            console.error(error);
        });
}

findMessage();


</script>

