<template>
    <div class="msgFormBox">
        <el-form ref="form" :model="formData" :rules="rules" label-width="80px">
            <!-- <el-form ref="form" :rules="rules" label-width="120px"> -->
            <el-form-item label="游戏ID" prop="name" :show-message="false">
                <el-input v-model="formData.name"></el-input>
            </el-form-item>
            <el-form-item label="邮箱地址" prop="email" :show-message="false">
                <el-input v-model="formData.email"></el-input>
            </el-form-item>
            <el-form-item label="留言" prop="text" :show-message="false">
                <el-input :autosize="{ minRows: 2}" resize="none" type="textarea" maxlength="200"
                    :show-word-limit="true" v-model="formData.text"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button style="font-weight: 700;color:#fff;width: 160px;" size="large" type="primary"
                    @click="submitForm">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import dayjs from "dayjs"

//用了 Vue 3 的 ref 函数来创建响应式数据。ref 函数返回一个响应式对象，该对象包含一个 value 属性，该属性存储了实际的数据值。我们使用 v-model 指令将表单元素与响应式数据绑定起来，这样当用户在表单中输入数据时，响应式数据也会自动更新。最后，我们定义了一个 submitForm 方法，用于提交表单数据。在方法中，我们可以通过访问响应式数据的 value 属性来获取表单数据。


const rules = {
    name: [
        { required: true, message: '请输入您的名称', trigger: 'blur' },
        { min: 4, max: 12, message: '长度在 4 到 12 个字符', trigger: 'blur' }],
    email: [
        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
    ],
    text: [
        { required: true, message: '请输入留言内容', trigger: 'blur' },
        { min: 2, max: 400, message: '长度在 2 到 200 个字符', trigger: 'blur' }]
}
const message = []

const formData = ref({
    name: '',
    email: '',
    text: '',
    time: ''
});

const nowDate = () => {
    formData.value.time = dayjs().format('YYYY-MM-DD HH:mm')
}

const submitForm = () => {


    nowDate()

    axios.post('http://localhost:3001/api/message/add', formData.value)
        .then(response => {
            console.log(response.data);
        })
        .catch(error => {
            console.error(error);
        });
}

</script>

<style lang="scss" scoped>
.msgFormBox {
    margin: 0 auto;
    min-width: 450px;
    max-width: 450px;
}
</style>